import React,{useEffect} from 'react'
import PropTypes from 'prop-types'
import { connect } from 'react-redux'
import {_adminGetVillaList} from "redux/actions";
import {Card, Table,Avatar,Button,Tooltip} from "antd";
import { CheckCircleOutlined,EyeOutlined,
    CloseCircleOutlined } from '@ant-design/icons';
import moment from "moment";
import {Link} from "react-router-dom";

const VillaList = (props)=> {
    let { _adminGetVillaList } = props
    console.log({props})

    useEffect(()=> {
        _adminGetVillaList({
            page:1,
            limit:10
        })
    },[_adminGetVillaList])

    const columns = [
        {
            dataIndex:'_id',
            key:"id",
            width:50,
            title:"No",
            render: (_,val,index)=> index +1
        },
        {
            dataIndex: "thumbnail",
            title:"Thumbnail",
            width:60,
            render:(_,val,index)=> <Avatar size={40} srcSet={val?.photos?.map((item)=> {
                return [item?.prefix_url,item?.filename].join("/")
            }).join(", ")} src={[_?.prefix_url,_?.filename].join("/")} shape={'square'}/>
        },
        {
            dataIndex:"name",
            key:"villa-name",
            title: "name"
        },
        {
            dataIndex:'contact',
            title:"Contact",
            render:(_,val)=> _?.phone_number ?? "-"
        },
        {
            dataIndex: "facility",
            title: "Facility",
            children:[
                {
                    title:"ac",
                    dataIndex:"facility",
                    width:40,
                    render: (_)=> _?.ac ? <CheckCircleOutlined />:<CloseCircleOutlined />
                },
                {
                    title:"tv",
                    dataIndex:"facility",
                    width:40,
                    render: (_)=> _?.tv ? <CheckCircleOutlined />:<CloseCircleOutlined />
                },
                {
                    title:"gazebo",
                    dataIndex:"facility",
                    width:40,
                    render: (_)=> _?.gazebo ? <CheckCircleOutlined />:<CloseCircleOutlined />
                },
                {
                    title:"wifi",
                    dataIndex:"facility",
                    width:40,
                    render: (_)=> _?.wifi ? <CheckCircleOutlined />:<CloseCircleOutlined />
                },
                {
                    title:"parking",
                    dataIndex:"facility",
                    width:40,
                    render: (_)=> _?.parking ? <CheckCircleOutlined />:<CloseCircleOutlined />
                }
            ]
        },
        {
            dataIndex: "createdAt",
            title:"Created",
            render:(_,val)=> _ ? moment(_).format("DD MMM YYYY") : "-"
        },
        {
            dataIndex: "updatedAt",
            title:"Updated",
            render:(_,val)=> _ ? moment(_).format("DD MMM YYYY") : "-"
        },
        {
            title:"",
            render: (_)=> {
                return (
                    <div className="text-right">
                        <Tooltip title={'View detail'}>
                            <Link to={`/app/villa/p/${_?._id}`}>
                                <Button size={'small'} icon={<EyeOutlined/>} className={'mr-2'}/>
                            </Link>
                        </Tooltip>
                        <Tooltip title={'Soft delete villa'}>
                            <Button size={'small'} icon={<CloseCircleOutlined/>}/>
                        </Tooltip>
                    </div>
                )
            }
        }
    ]
    return (
        <Card bordered={false} title={'Villa List'}>
            <Table
                loading={props?.loading ?? false}
                columns={columns}
                expandable={{
                    expandedRowRender: record => <p style={{ margin: 0 }}>{record?.description ?? "-"}</p>,
                    rowExpandable: record => record.name !== 'Not Expandable',
                }}
                dataSource={props?.data ?? []}
            />
        </Card>
    )
}

VillaList.propTypes = {
    _adminGetVillaList:PropTypes.func.isRequired
}
VillaList.defaultProps = {
    _adminGetVillaList: (query = {})=> {
        console.log({query})
    }
}

export default connect(({Villa})=> {
    let { admin } = Villa
    let { list } = admin
    return { ...list }
},{_adminGetVillaList})(React.memo(VillaList))
